<template>
  <div class="home">
    <v-row justify="center">
      <v-col cols="6">
        <v-card>
          <div class="d-flex flex-no-wrap justify-space-between">
            <div>
              <v-card-title
                class="headline"
                v-text="'Калькулятор расчета ТС'"
              ></v-card-title>

              <v-card-subtitle v-text="'Страничка находится в стадии разработки'"></v-card-subtitle>
            </div>

            <v-avatar
              class="ma-3"
              size="125"
              tile
            >
              <v-img src="../assets/calculator.jpg"></v-img>
            </v-avatar>
          </div>
        </v-card>

        <v-card>
          <v-card-text>

            <p class="font-weight-bold">Дата курсов: &nbsp;{{date}} USD - {{cbr_usd_rate}}; EUR - {{cbr_eur_rate}}</p>
            <p>Указать курсы вручную <input type="checkbox" id="checkbox" v-model="isHandMadeRates"></p>

            <p v-show="isHandMadeRates">
              <v-form>

                <v-text-field
                  label="USD:"
                  v-model="hand_usd_rate"
                  :error-messages="isInvalid_hand_usd_rate_message"
                ></v-text-field>
                <span>USD: <v-input v-model="hand_usd_rate"></v-input></span>
                <span>EUR: <v-input v-model="hand_eur_rate"></v-input></span>
              </v-form>
            </p>

            <p>
              Раходы в USD:
              <v-input v-model="usd_charges"></v-input>
            </p>
            <p>
              Раходы в EUR:
              <v-input v-model="eur_charges"></v-input>
            </p>
            <p>
              Раходы в RUB:
              <v-input v-model="rub_charges"></v-input>
            </p>
            <p class="font-weight-bold"> Таможенная стоимость: {{ result }}
              <span v-show="isHandMadeRates">*</span>
            </p>
            <span v-show="isHandMadeRates">* Расчет таможенной стоимости ведется по курсам заданным вручную!</span>
          </v-card-text>
        </v-card>

      </v-col>
    </v-row>

  </div>
</template>

<script>
    // @ is an alias to /src
    import HelloWorld from '@/components/HelloWorld.vue'
    import axios from 'axios'

    export default {
        name: 'home',
        components: {
            HelloWorld
        },
        data() {
            return {
                hand_usd_rate: 0,
                hand_eur_rate: 0,
                cbr_usd_rate: 0,
                cbr_eur_rate: 0,
                usd_charges: "",
                eur_charges: "",
                rub_charges: "",
                date: "!!! Курсы валют не загрузились. Проверь курсы!!!",
                isHandMadeRates: false
            }
        },
        computed: {
            result: function () {
                var usd_costs = this.usd_charges === "" ? 0 : eval(this.usd_charges);
                var eur_costs = this.eur_charges === "" ? 0 : eval(this.eur_charges);
                var rub_costs = this.rub_charges === "" ? 0 : eval(this.rub_charges);

                var result = usd_costs * this.usd_rate + eur_costs * this.eur_rate + rub_costs;

                return moneyFormat(result);
            },
            usd_rate: function () {
                return this.isHandMadeRates ? this.hand_usd_rate : this.cbr_usd_rate;
            },
            eur_rate: function () {
                return this.isHandMadeRates ? this.hand_eur_rate : this.cbr_eur_rate;
            },
            isInvalid_hand_usd_rate_message: function () {
                return this.hand_usd_rate === ""? "Введите курс USD" : "";
            },
            isInvalid_hand_eur_rate: function () {
                return this.hand_eur_rate === "";
            }
        },
        mounted: function () {
            axios
                .get("https://www.cbr-xml-daily.ru/daily_json.js")
                .then(response => {
                    this.cbr_usd_rate = response.data.Valute.USD.Value;
                    this.cbr_eur_rate = response.data.Valute.EUR.Value;
                    this.date = response.data.Date.substring(0, 10);

                    this.hand_usd_rate = this.cbr_usd_rate;
                    this.hand_eur_rate = this.cbr_eur_rate;
                })
                .catch(function (e) {
                    alert("Что-то пошло не так с загрузкой курсов. " + e);
                });
        },
    }

    function moneyFormat(n) {
        return parseFloat(n).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1 ").replace('.', ',');
    }
</script>
